  background-color: var(--base-button-background);
  color: var(--base-button-button-text);
  transition: background-color ease-in 200ms;
}
.button.ghost.white, input[type=submit].ghost.white, button.ghost.white, button.button.ghost.white, a.button.ghost.white {
  border-color: var(--light-main);
  color: var(--light-main);
}
.button.ghost.small, input[type=submit].ghost.small, button.ghost.small, button.button.ghost.small, a.button.ghost.small {
  font-size: var(--s-2);
  padding: var(--s-4) var(--s1);
}
.button.secondary, input[type=submit].secondary, button.secondary, button.button.secondary, a.button.secondary {
  display: inline-block;
  background-color: transparent;
  color: var(--base-body);
  border: none;
  width: auto;
  min-height: unset;
  text-align: left;
  border-bottom: 1px solid transparent;
  padding: 0;
  border-radius: 0;
  font-weight: var(--link-weight);
}
.button.secondary:after, input[type=submit].secondary:after, button.secondary:after, button.button.secondary:after, a.button.secondary:after {
  display: inline;
  font-family: "Font Awesome 5 Free";
  content: "  \f054";
  font-weight: 900;
  padding: 0 5px 5px 0;
}
.button.secondary:hover, input[type=submit].secondary:hover, button.secondary:hover, button.button.secondary:hover, a.button.secondary:hover {
  text-decoration: none;
  border-bottom: 1px solid var(--primary-main);
  transition: border ease-in 200ms;
}
.button.full-width, input[type=submit].full-width, button.full-width, button.button.full-width, a.button.full-width {
  width: 100%;
}
.button.load-more, input[type=submit].load-more, button.load-more, button.button.load-more, a.button.load-more {
  width: 100%;
  box-sizing: border-box;
}
.button:active, input[type=submit]:active, button:active, button.button:active, a.button:active {
  transform: scale(0.95);
}

input[type=submit] {
  border-style: none;
}

a.abs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 3;
}

.button-wrap {
  display: inline-flex;
  width: 100%;
  flex-wrap: wrap;
}
.button-wrap.left {
  justify-content: flex-start;
}
.button-wrap.right {
  justify-content: flex-end;
}
.button-wrap.center {
  justify-content: center;
}
.button-wrap.space-around {
  justify-content: space-around;
}
.button-wrap.space-between {
  justify-content: space-between;
}
.button-wrap.space-evenly {
  justify-content: space-evenly;
}
.button-wrap.evenStretch {
  justify-content: space-evenly;
  gap: var(--s1);
}
.button-wrap.evenStretch > * {
  flex: 1 1 auto;
}

@media only screen and (max-width: 767px) {
  .fullWidthMobile {
    flex-direction: column;
    align-content: stretch;
  }
  .fullWidthMobile a:not(.secondary) {
    width: 100%;
    box-sizing: border-box;
  }
}

a figure img:hover {
  transition: all 200ms ease-in-out;
  transform: scale(var(--hover-scale));
}

figure {
  border-radius: var(--image-border-radius);
  overflow: hidden;
}

img {
  border-radius: var(--image-border-radius);
}

.graphic {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: auto;
  height: auto;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
}

/* Start Recruitment Masonry Template */
